import { notFound } from "next/navigation";
import { prisma } from "~/server/db";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";
import { Button } from "~/components/ui/button";
import { Input } from "~/components/ui/input";
import { Label } from "~/components/ui/label";
import { Textarea } from "~/components/ui/textarea";

interface Props {
    params: {
        teamId: string;
    };
}

export default async function EditTeamPage({ params }: Props) {
    const teamId = parseInt(params.teamId);

    if (isNaN(teamId)) {
        notFound();
    }

    const team = await prisma.team.findUnique({
        where: { id: teamId },
        include: {
            users: true,
            game: true,
        },
    });

    if (!team) {
        notFound();
    }

    return (
        <div className="max-w-2xl">
            <div className="mb-8">
                <h1 className="text-3xl font-bold tracking-tight">Edit Team</h1>
                <p className="text-slate-400">Update team details and manage players.</p>
            </div>

            <Card className="border-slate-800 bg-slate-900/50">
                <CardHeader>
                    <CardTitle>Team Details</CardTitle>
                </CardHeader>
                <CardContent>
                    <form action="/api/teams/update" method="POST" className="space-y-6">
                        <input type="hidden" name="teamId" value={team.id} />

                        <div className="space-y-2">
                            <Label htmlFor="teamName">Team Name</Label>
                            <Input
                                id="teamName"
                                name="teamName"
                                defaultValue={team.name}
                                required
                                className="bg-slate-950"
                            />
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="gameId">Game ID</Label>
                            <Input
                                id="gameId"
                                name="gameId"
                                type="number"
                                defaultValue={team.gameId}
                                required
                                className="bg-slate-950"
                            />
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="playerEmails">
                                Player Emails
                                <span className="ml-1 text-sm text-slate-400">
                                    (one per line or comma-separated)
                                </span>
                            </Label>
                            <Textarea
                                id="playerEmails"
                                name="playerEmails"
                                defaultValue={team.users.map(user => user.email).join("\n")}
                                required
                                className="min-h-[120px] bg-slate-950"
                            />
                        </div>

                        <div className="flex justify-end gap-4">
                            <Button
                                type="button"
                                variant="outline"
                                onClick={() => window.history.back()}
                            >
                                Cancel
                            </Button>
                            <Button type="submit">
                                Save Changes
                            </Button>
                        </div>
                    </form>
                </CardContent>
            </Card>
        </div>
    );
} 